import React, { useRef, useEffect } from 'react';
import styles from '../css/alert.module.css';
import { IoMdClose, IoMdCheckbox } from 'react-icons/io';

export interface AlertProps {
	message: string
	title?: string
	confirm?: boolean
	resolve?: () => void
	reject?: () => void
	close: () => void
}

export default function Alert (props: AlertProps) {
	
    const message = typeof props.message === 'string' ? props.message : JSON.stringify(props.message, null, 4);

    const alertBox = useRef(null as HTMLButtonElement);
    useEffect(() => {
        alertBox.current.focus();
    }, [alertBox]);

    return (
		<div className={styles.shade} onClick={(e) => e.stopPropagation()}>
			<div className={['container', styles.container].join(' ')}>
				<div className={styles.alertBox}>
					{props.title ? <div className={styles.title}>{props.title}</div> : null}
					<div className={[styles.textBox].join(' ')}>
						{message}
					</div>
					{
						!props.confirm ?
							<button className={[styles.OK, 'button'].join(' ')} onClick={props.close} ref={alertBox}>
								OK
							</button> :
							<>
								<button className={[styles.NO, 'button'].join(' ')} ref={alertBox} onClick={() => {
									props.reject();
									props.close();
								}}>
									<IoMdClose />
								</button>
								<button className={[styles.YES, 'button'].join(' ')} onClick={() => {
									props.resolve();
									props.close();
								}}>
									<IoMdCheckbox />
								</button>
							</>
					}
				</div>
			</div>
		</div>
    );

}